@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: #202020;
}

:root {
  --linkcolor: rgb(246, 233, 160);
  --textcolor: rgb(209, 206, 206);
}

.sidebar-top {
  grid-area: sidebar-top;
}

.sidebar-mid {
  grid-area: sidebar-mid;
}

.sidebar-bot {
  grid-area: sidebar-bot;
}

.leftpanel {
  grid-area: leftpanel;
  color: rgb(209, 206, 206);
  color: var(--textcolor);
}

.rightpanel {
  grid-area: rightpanel;
}

@media (max-width: 991px) {
  .container {
    display: grid;
    grid-template-columns: 11fr 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      "sidebar-mid sidebar-bot"
      "leftpanel leftpanel";
  }
  
  .instaimg, .rightpanel {
    display: none;
  }

  .leftpanel {
    border-top: 1px solid rgb(209, 206, 206);
    border-top: 1px solid var(--textcolor);
    padding: 20px 30px;
  }

  .sidebar-mid {
    margin: left;
    position: -webkit-sticky;
    position: sticky;
    padding: 6px 10px;
  }

  .sidebar-bot {
    margin: right;
    position: -webkit-sticky;
    position: sticky;
    padding: 6px 10px;
  }

  .sidebar-mid ul li, .sidebar-bot {
    display: inline-block;
    margin-top: 4px;
  }

  .sidebar-mid ul li {
    padding: 0 10px;
  }

  .sidebar ul li .active {
    color: rgb(246, 233, 160);
    color: var(--linkcolor);
  }

  .material-icons {
    font-size: 30px;
  }

  a {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
  }

  .githubicon {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
    font-size: 30px;
  }

}

/* Large devices (desktops, 992px and up)*/
@media (min-width: 992px) {

  .container {
    display: grid;
    height: 100vh;
    grid-template-columns: 4% 46% 50%;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas:
      "sidebar-top leftpanel rightpanel"
      "sidebar-mid leftpanel rightpanel"
      "sidebar-bot leftpanel rightpanel";
  }
  
  .sidebar {
    margin: auto;
  }

  .sidebar ul {
    list-style-type: none;
    margin: auto;
  }

  .sidebar ul li {
    padding: 10px 0;
    margin: auto;
    border-bottom: 1px solid transparent;
  }

  .sidebar ul li span:hover {
    color: rgb(246, 233, 160);
    color: var(--linkcolor);
  }

  .sidebar ul li .active {
    border-bottom: 1px solid rgb(246, 233, 160);
    border-bottom: 1px solid var(--linkcolor);
    color: rgb(246, 233, 160);
    color: var(--linkcolor);
  }

  .sidebar ul {
    margin: auto;
  }

  .sidebar-top {
    margin-top: 0;
  }

  .leftpanel, .rightpanel {
    background-color: #252525;
  }

  .material-icons {
    font-size: 30px;
  }

  .sidebar-bot {
    margin-bottom: 10px;
  }

  .githubicon {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
    font-size: 2.5vw;
  }

  .githubicon:hover {
    color:rgb(246, 233, 160);
    color:var(--linkcolor);
  }

  .instaimg {
    width: 100%;
  }

  a {
    color: rgb(209, 206, 206);
    color: var(--textcolor);
  }
}

